import React, {useEffect, useState} from 'react'
import StoryCards from "../../containers/StoryCards/StoryCards"

// eslint-disable-next-line import/no-anonymous-default-export
export default (props) => {
    const [data, setData] = useState();
    const [error, setError] = useState();

    useEffect(() => {
        let CORS = `https://cors-anywhere.herokuapp.com/`;
        let server = `https://aggregatr-server.herokuapp.com/getStories`;
        try {
            fetch(`${CORS}${server}`)
                .then(data => data.json())
                .then(setData)
                .catch(setError);
        } catch {
            return null
        }
    }, []);
    if (error) return (<pre>{JSON.stringify(error)}</pre>)
    if (!data) return null;

    return (
        <>
            <div className="container">
                <div className="row">
                    {
                        data.map(story => <StoryCards key={(story.header).replace(/ /g, '-')} article={story}/>)
                    }
                </div>
            </div>
        </>
    );
}