import './App.css';
import {Route, Switch} from 'react-router-dom';
import Home from '../Home/Home';
import StoryList from "../List/StoryList";
import Advertise from "../Advertise/Advertise";
import Donate from "../Donate/Donate";
import Reader from "../Reader/Reader";

function App() {
    return (
        <div className="App">
            <Switch>
                <Route exact path="/" component={Home}/>
                <Route exact path="/news" component={StoryList}/>
                <Route exact path="/entertainment" component={StoryList}/>
                <Route exact path="/viral" component={StoryList}/>
                <Route exact path="/sports" component={StoryList}/>
                <Route exact path="/lifestyle" component={StoryList}/>
                <Route exact path="/videos" component={StoryList}/>
                <Route exact path="/advertise" component={Advertise}/>
                <Route exact path="/donate" component={Donate}/>
                <Route exact path="/reader" component={Reader}/>
            </Switch>
        </div>
    );
}

export default App;
