import React from 'react';
import {Link} from 'react-router-dom';
import './StoryCards.css';

function formatImageUrl(imageString) {
    if (imageString !== undefined) {
        return imageString;
    }
    return "data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAARwAAACxCAMAAAAh3/JWAAAAA1BMVEWpqamhHEfZAAAASElEQVR4nO3BMQEAAADCoPVPbQ0PoAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAIALA8UNAAFusnLHAAAAAElFTkSuQmCC";
}

export default (props) =>
    (
        <>
            {/* eslint-disable-next-line no-unused-vars */}
            <div className="col-sm-3" style={{marginBottom: "20px"}}>
                <div className="card" style={{borderRadius: 0, border: 0}}>
                    <div className="img-container">
                        <img src={formatImageUrl(props.article.images[0])}
                             className="card-img-top" style={{borderTopLeftRadius: 0, borderTopRightRadius: 0}}
                             alt="" loading="lazy"/>
                    </div>
                    <div className="card-body" style={{padding: "2px"}}>
                        <Link to={
                            {
                                pathname: "reader",
                                state: {
                                    story: props.article
                                }
                            }} className="nav-link" style={{padding: "0px"}}>
                            <h5 style={{fontSize: '16px', fontWeight: 'bolder'}} className="card-title">{props.article.header}</h5>
                        </Link>
                        <p style={{fontSize: '12px'}} className="card-text story-text">
                            <span>{props.article.author}</span> <br/> <span>{props.article.storyDate}</span>
                        </p>
                    </div>
                </div>
            </div>
        </>
    );
